import {useState, useEffect} from "react"
import Card from "./Card"

export default function Portfolio(){
    const [data, setData] = useState([])
    useEffect(async ()=>{
        const res = await fetch("https://rosarioterranova.github.io/projects.json")
        const data = await res.json()
        setData(data)
    },[])

    return <div className="container">
        <h4 className="text-center my-3 text-dark" id="webapps">Web Apps</h4>
        {data.filter(el => el.category == "web").map(el => <Card title={el.title} description={el.description} image={el.img} link={el.link} linkCode={el.linkCode} /> )}
        
        <h4 className="text-center my-3 text-dark" id="mobileapps">Mobile Apps</h4>
        {data.filter(el => el.category == "mobile").map(el => <Card title={el.title} description={el.description} image={el.img} link={el.link} linkCode={el.linkCode} /> )}
        
        <h4 className="text-center my-3 text-dark" id="games">Games</h4>
        {data.filter(el => el.category == "games").map(el => <Card title={el.title} description={el.description} image={el.img} link={el.link} linkCode={el.linkCode} /> )}

    </div>

}